@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 0;
  font-family: Merriweather, Arial, Helvetica, sans-serif;
  background-color: #fafafa;
}

.container{
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.titles-container{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.maintTitle {
  font-size: 50px;
  text-align: center;
  margin: 10px;
}

.authorTitle{
  text-align: center;
}


.button-container{
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 30px;
}

.dialogue-container{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.displayWeather{
  justify-content: center;
  align-items: center;
  text-align: center;
  display: inline-block;
  border: 1px solid #EC6E4C;
  border-radius: 8px;
  margin-top: 20px;
}

.coat-check{
  font-size: 35px;
  text-align: center;
}

.description-container{
  display: flex;
  justify-content: center;
  align-items: center;
}

.weather-image{
  max-width: 10%;
  height: auto;
}

.button{
  background-color: #EC6E4C;
  border: none;
  border-radius: 15px;
  color: white;
  padding: 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}
